<template>
<div id="app" class="container">
    <div>
      <div class="form-group">
        <label for="qnaUserid">작성자</label>
        <input type="text" class="form-control" id="qnaUserid" ref="qnaUserid" placeholder="작성자를 입력하세요" v-model="qnaUserid">
      </div>
      <div class="form-group">
        <label for="qnaTitle">제목</label>
        <input type="text" class="form-control" id="qnaTitle" ref="qnaTitle" placeholder="제목을 입력하세요" v-model="qnaTitle">
      </div>
      <div class="form-group">
        <label for="qnaContent">내용</label>
        <textarea type="text" class="form-control" id="contnet" ref="qnaContent" placeholder="내용을 입력하세요" v-model="qnaContent"></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-primary" @click="checkHandler">질문등록</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    name:'Create',
    data: function () {
    return {
      qnaNo: '',
      qnaTitle: '',
      qnaContent: '',
      qnaUserid: '',
      qnaDatetime: '',
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = '';
      !this.qnaTitle && ((msg = '제목을 입력해주세요'), (err = false), this.$refs.qnaTitle.focus());
      err && !this.qnaContent && ((msg = '내용을 입력해주세요'), (err = false), this.$refs.qnaContent.focus());
      err && !this.qnaUserid && ((msg = '작성자를 입력해주세요'), (err = false), this.$refs.qnaUserid.focus());
      if (!err) alert(msg);
      else this.createHandler();

    },
    createHandler() {
      axios
        .post('http://localhost:9999/happyhouse/api/qna', {
          qnaUserid:this.qnaUserid,
          qnaTitle:this.qnaTitle,
          qnaContent:this.qnaContent,
        })
        .then(({ data }) => {
          let msg = '등록 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '등록이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        });
    },
    moveList() {
      this.$router.push('/list');
    },
  },
}
</script>

