<template>
  <div id="app" class="container">
    <div>
      <div class="form-group">
        <label for="qnaUserid">질문 작성자: </label>
        <a> {{item.qnaUserid}}</a>
      </div>
      <div class="form-group">
        <label for="qnaTitle">질문 제목: </label>
        <a> {{item.qnaTitle}}</a>
      </div>
      <div class="form-group">
        <label for="qnaContent">질문 내용: </label>
         <a> {{item.qnaContent}}</a>
      </div>

      <div class="form-group">
       <label for="replyUserid">작성자</label>
        <input type="text" class="form-control" id="replyUserid" ref="replyUserid" placeholder="작성자을 입력하세요" v-model="replyUserid">
      </div>
      <div class="form-group">
        <label for="replyContent">내용</label>
        <textarea type="text" class="form-control" id="contnet" ref="replyContent" placeholder="내용을 입력하세요" v-model="replyContent"></textarea>
      </div>

      
      <div class="text-right" >
        <button class="btn btn-primary" @click="updateHandler">완료</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Update',
  data: function() {
    return {
      item: [],
      qnaNo: '',
      qnaTitle: '',
      qnaContent: '',
      qnaUserid: '',
      replyUserid : '',
      replyContent: '',
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    this.qnaNo = params.get('qnaNo');
    axios.get(`http://localhost:9999/happyhouse/api/qna/${params.get('qnaNo')}`).then(({ data }) => {
      this.item = data;
      this.replyUserid = data.replyUserid;
      this.replyContent = data.replyContent;
      });
    },
  methods: {
    updateHandler() {
      axios
      .put('http://localhost:9999/happyhouse/api/qna/'+ this.qnaNo, {
          qnaNo: this.qnaNo,
          replyUserid: this.replyUserid,
          replyContent: this.replyContent,
        })
        .then(({ data }) => {
          let msg = '답변 수정 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '답변 수정이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('답변 수정 처리시 에러가 발생했습니다.');
        });
    },
    moveList() {
      this.$router.push('/list');
    },
  }
};
</script>

