<template>
  <div id="app" class="container">
    <div>
      <div class="form-group">
        
        <label for="qnaUserid">작성자</label>
        <input type="text" class="form-control" id="qnaUserid" ref="qnaUserid"  v-model="qnaUserid">
      </div>
      <div class="form-group">
        <label for="qnaTitle">제목</label>
        <input type="text" class="form-control" id="qnaTitle" ref="qnaTitle" placeholder="제목을 입력하세요" v-model="qnaTitle">
      </div>
      <div class="form-group">
        <label for="qnaContent">내용</label>
        <textarea type="text" class="form-control" id="contnet" ref="qnaContent" placeholder="내용을 입력하세요" v-model="qnaContent"></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-primary" @click="updateHandler">완료</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Update',
  data: function() {
    return {
      item: [],
      qnaNo: '',
      qnaTitle: '',
      qnaContent: '',
      qnaUserid: '',
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    this.qnaNo = params.get('qnaNo');
    axios.get(`http://localhost:9999/happyhouse/api/qna/${params.get('qnaNo')}`).then(({ data }) => {
      this.item = data;
      this.qnaUserid = data.qnaUserid;
      this.qnaTitle = data.qnaTitle;
      this.qnaContent = data.qnaContent;
      });
    },
  methods: {
    updateHandler() {
      axios
      .put('http://localhost:9999/happyhouse/api/qna/', {
          qnaNo: this.qnaNo,
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
        })
        .then(({ data }) => {
          let msg = '수정 처리시 문제가 발생했습니다.';
          if (data === 'success') {
            msg = '수정이 완료되었습니다.';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('수정 처리시 에러가 발생했습니다.');
        });
    },
    moveList() {
      this.$router.push('/list');
    },
  }
};
</script>

