<template>
   <div id="app" class="container">
    <br><br><h4 class="text-center">질문목록</h4>
    <!-- <input type="text" v-model="ename" placeholder="질문을 입력하세요"/>
      <button class="btn btn-primary" @click="ename">검색</button> -->
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{width: '10%'}" />
          <col :style="{width: '25%'}" />
          <col :style="{width: '25%'}" />
          <col :style="{width: '25%'}" />
          <col :style="{width: '15%'}" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>글쓴이</th>
          <th>등록시간</th>
          <th>답변</th>
        </tr>
        <tr v-for="(board, index) in items" :key="index + '_items'">
          <td>{{board.qnaNo}}</td>
          <td><router-link :to="'read?qnaNo=' + board.qnaNo">{{board.qnaTitle}}</router-link></td>
          <td>{{board.qnaUserid}}</td>
          <td>{{getFormatDate(board.qnaDatetime)}}</td>
          <td>
            <div v-if = "board.replyUserid === null">
              X
              </div>
            <div v-else>O</div>
          </td>
        </tr>
        
      </table>
    </div>
    <div v-else>
        글이 없습니다.
    </div>
    <div class="text-right">
        <button class="btn btn-primary" @click="movePage">질문 등록</button>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';

export default {
    name: 'List',
    data: function () {
      return {
        items: [],
        ename:''
        
      };
    },
    created() {
      axios.get('http://localhost:9999/happyhouse/api/qna').then(({ data }) => {
        this.items = data;
       
      });
    },
    methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD');
    },
    movePage() {
      this.$router.push('/create');
    },
  },
}
</script>

